import React, { Component } from 'react';


export function CreateContent() {
    return(
        <div >
            <div>1</div>
            <div>2</div>
            <div>3</div>
            <div>4</div>
            <div>5</div>
        </div>
    );
}

export default class Create extends Component{
    constructor(props){
        super(props);

        this.state = {
            contextVisible: false,
        };
    }

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem('create');
        this.setState({
            contextVisible: !this.state.contextVisible,
        });
        console.log('this.state.contextVisible');
    }

    render(){
        let temp = 'toolsItem create';
        if(this.props.tools.currentItem === 'create')
            temp += ' active';

        let context = null;

        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
                {context}
            </div>

        );
    }
}